import { supabase } from '@/lib/supabase';

export default async function Home() {
  const { data: cars, error } = await supabase
    .from('cars')
    .select('id, stock_id, make, model, year, price, main_image, featured')
    .order('created_at', { ascending: false });

  if (error) return <p className="text-red-500 p-10">Error: {error.message}</p>;
  if (!cars?.length) return <p className="p-10">No cars found. Add one in Supabase!</p>;

  return (
    <main className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-5xl font-bold text-center mb-4 text-blue-900">
          Car Export Site
        </h1>
        <p className="text-center text-gray-600 mb-12">
          Live data from Supabase — Add cars anytime!
        </p>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
          {cars.map(car => (
            <div key={car.id} className="bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-2xl transition">
              {car.main_image ? (
                <img 
                  src={car.main_image} 
                  alt={`${car.make} ${car.model}`}
                  className="w-full h-56 object-cover"
                />
              ) : (
                <div className="bg-gray-200 border-2 border-dashed rounded-t-xl w-full h-56" />
              )}
              <div className="p-6">
                <h2 className="text-2xl font-bold text-gray-800">
                  {car.year} {car.make} {car.model}
                </h2>
                <p className="text-3xl font-bold text-green-600 mt-2">
                  ${car.price.toLocaleString()}
                </p>
                <p className="text-sm text-gray-500 mt-1">
                  Stock: {car.stock_id} • {car.featured && 'Featured'}
                </p>
                <button className="mt-4 w-full bg-blue-600 text-white py-3 rounded-lg font-semibold hover:bg-blue-700 transition">
                  View Details
                </button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </main>
  );
}